

import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment'

@Injectable({
  providedIn: 'root'
})
export class UserService {

  constructor(private http: HttpClient) { }
  baseURL = environment.baseURL;

  login(email, password) {
    return new Promise((resolve, reject) => {
      this.http
        .post(this.baseURL + '/user/login', { email: email, password: password })
        .subscribe(
          json => {
            resolve(json);
          },
          error => {
            reject(error);
          }
        );
    });
  }
  register(username, email, password) {
    return new Promise((resolve, reject) => {
      this.http
        .post(this.baseURL + '/user/register', { username: username, email: email, password: password })
        .subscribe(
          json => {
            resolve(json);
          },
          error => {
            reject(error);
          }
        );
    });
  };
  
  updateUser(idusers, username, email, password, description, role) {
    return new Promise((resolve, reject) => {
      this.http
        .post(this.baseURL + '/user/updateUser', {idusers: idusers,username: username, email: email, password: password, description: description, role: role })
        .subscribe(
          json => {
            resolve(json);
          },
          error => {
            reject(error);
          }
        );
    });
  };
  createUserByAdmin(username, email, password, description, role) {
    return new Promise((resolve, reject) => {
      this.http
        .post(this.baseURL + '/user/createUserByAdmin', {username: username, email: email, password: password, description: description, role: role })
        .subscribe(
          json => {
            resolve(json);
          },
          error => {
            reject(error);
          }
        );
    });
  };

  getAllUsers() {
    return new Promise((resolve, reject) => {
      this.http
        .get(this.baseURL + '/user/getAllUsers')
        .subscribe(
          json => {
            resolve(json);
          },
          error => {
            reject(error);
          }
        );
    });
  }
}

